@import url(https://fonts.googleapis.com/css?family=Exo+2:300,400,500,600,700,800,900&display=swap&subset=cyrillic);
body{margin:0;font-family:'Exo 2', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:16px;font-weight:400}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{width:100%;box-sizing:border-box;padding:calc((100vh / 100) * 2) calc((100vw / 100) * 5)}.install{position:absolute;height:150px;width:200px;background-color:#fff;display:none;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:9999}@media screen and (min-width: 250px) and (max-width: 760px){.App{padding:0}}

.block{background-color:#c3c3c3;color:#373737;position:absolute;top:1rem;left:1rem;width:250px;height:150px;padding:10px}

.loading{background:rgba(255,255,255,0.3);width:100%;height:100vh;z-index:9999;position:relative}.loading__body{position:absolute;height:auto;width:auto;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.register{position:absolute;padding:0 10px;width:320px;height:450px;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);overflow-y:scroll}.register h3{text-align:center}.register::-webkit-scrollbar{width:3px;height:3px}.register::-webkit-scrollbar-track{background-color:#999}.register::-webkit-scrollbar-track-piece{background-color:#c3c3c3}.register::-webkit-scrollbar-thumb{height:50px;background-color:#666;border-radius:3px}.register::-webkit-scrollbar-corner{background-color:#999}.register::-webkit-resizer{background-color:#666}

.login{width:250px;height:auto;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.header__avatar{padding:5px 6px;border-radius:50%;text-shadow:#000}.header__name{padding-left:10px;cursor:pointer}


.header{background:#66f;padding:15px;color:#fff;display:flex;justify-content:space-between;align-items:center}.header__info{display:flex;align-items:center}.header__brand{width:170px}.content{background:#f0f0ff;color:#373737;height:calc(100vh - 90px);padding:15px}

